import React from 'react';

function CourseSummary() {
  return (
    <div class='module'>
      <h2>Section 2:</h2>
      <h2 style={{ borderBottom: '2px solid #333', paddingBottom: '10px' }}>BUS118W - Web Based Computing</h2>
      <p>
        In the Web-based Computing course offered by Lucas College and Graduate School of Business, students are immersed in an 
        intensive technical training that covers the foundational and advanced concepts of web application development. The course 
        equips students with hands-on expertise in designing web applications, mastering user interface design through wireframe 
        creation and mock-up pages, and programming both client and server-side applications using languages and technologies such 
        as HTML, CSS, and JavaScript. Additionally, learners gain practical experience in building fully functional websites, with 
        an emphasis on utilizing industry-standard tools like Visual Studio Code. By the conclusion of the course, students are adept 
        in a range of web development skills, ready to innovate in the dynamic digital landscape.
      </p>
    </div>
  );
}

export default CourseSummary;
