function WorkExperience({ title, company, points }) {
  const listStyle = {
    marginBottom: '10px',
  };

  const bulletPointStyle = {
    marginLeft: '20px',
    listStyleType: 'disc',
  };

  return (
    <li style={listStyle}>
      {title} - {company}
      <ul>
        {points.map((point, index) => (
          <li key={index} style={bulletPointStyle}>
            {point}
          </li>
        ))}
      </ul>
    </li>
  );
}

