import React from 'react';

const EmbeddedMap = () => {
    // Construct Google Maps embed url
    const embedURL = `https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3172.309222088388!2d-121.88364642414649!3d37.33518737209935!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x808fccb864de43d5%3A0x397ffe721937340e!2sSan%20Jos%C3%A9%20State%20University!5e0!3m2!1sen!2sus!4v1695966565931!5m2!1sen!2sus`;

    return (
        <div style={{ width: '80%', height: '400px', margin: '20px 0' }}>
        <h2>Section 4:</h2>
            <iframe
                title='San Jose State University'
                width='100%'
                height='100%'
                frameborder='0'
                scrolling='no'
                marginHeight='0'
                marginWidth='0'
                src={embedURL}
            ></iframe>
        </div>
    );

};

export default EmbeddedMap;