import React from 'react';
import EmbeddedVideo from './EmbeddedVideo';

function VideoSummary() {
    return (
        <div class="module">
            {/* Embedded Video */}
            <h2>Section 3:</h2>
            <EmbeddedVideo />

            <h2>Review of the Lecture:</h2>
            <p>The lecture provides an in-depth introduction to the concept of HTTP cookies. It begins by explaining the fundamental nature of cookies, their applications, and their significance in the digital world. The presenter delves into the different types of cookies, including session cookies, persistent cookies, secure cookies, HTTP-only cookies, same-site cookies, and third-party cookies. Each type is explained with its unique characteristics and use cases. The lecture also touches upon the practical applications of cookies in session management, personalization, and tracking. Towards the end, the lecture addresses the privacy concerns associated with cookies, especially third-party cookies, and the implications for user trust and legislative actions.</p>

            <h2>What I learned:</h2>
            <ol>
                <li>A cookie, also known as an HTTP cookie, is a small piece of data sent from a web server and stored in the user's web browser while browsing</li>
                <li>Cookies have various applications, including tracking user activity, storing stateful information, and remembering user inputs</li>
                <li>
                There are several types of cookies, each with its own unique characteristics:
                    <ol>
                    <li>
                        Session Cookie: Temporary and deleted when the browser is closed.
                    </li>
                    <li>
                        Persistent Cookie: Cannot be accessed by client-side APIs like Javascript
                    </li>
                    <li>
                        HTTP-Only Cookie: Sent only in requests originating from the same domain
                    </li>
                    </ol>
                </li>
                <li>Cookies play a vital role in session management, personalization based on user preferences, and tracking web browsing habits</li>
                <li>Privacy concerns arise from the potential misuse of cookies, especially third-party cookies, leading to the tracking of users across multiple domains</li>
            </ol>

            <h2>Suggestions for Further Improvement</h2>
            <ol>
                <li>This lecture could benefit from visual demonstrations or animations that show how cookies work in real-time, making it easier for viewers to grasp the concept</li>
                <li>Incorporating real-world case studies can help in understanding the practical implications and uses of cookies such as when websites are requesting to activate ALL or no cookies</li>
            </ol>
        </div>
    )
}

export default VideoSummary;